.header {
  min-height: 9.375rem;
  background: #ffffff;
  display: flex;
  justify-content: start;
  padding: 0 3.125rem 0 6.25rem;
  position: relative;
}

.header_descr {
  margin-top: 1.875rem;
  width: 18.75rem;
}

.header_descr_title {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.75rem;
  color: #000000;
  margin-bottom: 0.3125rem;
}

.header_descr_subtitle {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1rem;
  color: #000000;
  margin-top: 0.0625rem;
}

.header::after {
  content: '';
  width: 100%;
  height: 0.125rem;
  background: #000000;
  position: absolute;
  left: 0;
  top: 9.25rem;
}

.header_img {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end;
  align-content: end;
  margin-right: 6.25rem;
}

.account {
  width: 8.125rem;
  min-height: 9.375rem;
}

.account_logo {
  background: #C4C4C4;
  border-radius: 100%;
  width: 4.375rem;
  height: 4.375rem;
  margin-left: 1.875rem;
  margin-top: 0.625rem;
}

.menu {
  width: 15rem;
  min-height: calc(100vh - 190px);
  padding: 1.25rem 1.875rem;
  border-right: 0.125rem solid #000000;
}

.menu ul {
  list-style: none;
  width: 15rem;
  padding: 0;
}

.menu-group {
  padding-left: 0.625rem;
}

.menu-item {
  min-height: 1.875rem;
  font-weight: 400;
  color: #000000;
  line-height: 1.875rem;
  margin-top: 1.25rem;
  position: relative;
}

.menu-item a {
  display: block;
  text-decoration: none;
  color: #000000;
  padding-left: 0.625rem;
  cursor: pointer;
}

.menu-item a:hover {
  background: #C4C4C4;
}

.menu-item::before {
  content: '';
  width: 15rem;
  height: 0.125rem;
  background: #000000;
  position: absolute;
  top: 1.75rem;
  left: 0;
}

.menu-item__active {
  background: #000000;
}

.menu-item__active a {
  color: #ffffff;
}

.menu-item__active a:hover {
  background: #000000;
}

.menu-subitem {
  min-height: 1.875rem;
  font-weight: 400;
  color: #000000;
  line-height: 1.875rem;
  position: relative;
}

.menu-subitem a {
  padding-left: 1.875rem;
  display: block;
  text-decoration: none;
  color: #000000;
  cursor: pointer;
}

.menu-subitem a:hover {
  background: #C4C4C4;
}

.menu-subitem::before {
  content: '';
  width: 0.625rem;
  height: 0.625rem;
  border-radius: 3.125rem;
  background: #000000;
  position: absolute;
  top: 0.625rem;
  left: 0.625rem;
}

.menu-subitem__active {
  background: #000000;
}

.menu-subitem__active a {
  color: #ffffff;
}

.menu-subitem__active a:hover {
  background: #000000;
}

.menu-subitem__active::before {
  background: #ffffff;
}

.parts-table {
  margin-top: 0.9375rem;
}

.parts-table-header {
  border-top: 2px solid #000000;
  border-bottom: 2px solid #000000;
  height: 2.6875rem;
  display: flex;
}

.parts-table-header-index {
  width: 3.125rem;
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 2.6875rem;
  text-align: center;
  color: #000000;
  user-select: none;
}

.parts-table-header-name {
  flex-grow: 1;
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 2.6875rem;
  text-align: center;
  color: #000000;
  display: flex;
  justify-content: start;
  padding-left: 0.625rem;
  cursor: pointer;
  user-select: none;
}

.parts-table-header-name span {
  display: block;
  margin-left: 0.625rem;
}

.parts-table-header-edit {
  width: 6.25rem;
  background: #ffffff;
}

.parts-table-header-delete {
  width: 6.25rem;
  background: #ffffff;
}

.parts-table-row {
  border-bottom: 1px solid #000000;
  display: flex;
  height: 2.1875rem;
}

.parts-table-row-index {
  width: 3.125rem;
  font-weight: 400;
  font-size: 1rem;
  line-height: 2.1875rem;
  text-align: center;
  color: #000000;
}

.parts-table-row-name {
  flex-grow: 1;
  font-weight: 400;
  font-size: 1rem;
  line-height: 2.1875rem;
  text-align: start;
  padding-left: 1.875rem;
  color: #000000;
}

.parts-table-row-edit {
  width: 6.25rem;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.parts-table-row-edit-img {
  width: 1.375rem;
  height: 1.375rem;
  background: url("../img/icons/edit-icon.svg") center center/contain no-repeat;
}

.parts-table-row-edit:hover {
  background: #8BA1B6;
}

.parts-table-row-edit:active {
  background: #ffffff;
}

.parts-table-row-delete {
  width: 6.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.parts-table-row-delete-img {
  width: 1.375rem;
  height: 1.375rem;
  background: url("../img/icons/delete-icon.svg") center center/contain no-repeat;
}

.parts-table-row-delete:hover {
  background: #8BA1B6;
}

.parts-table-row-delete:active {
  background: #ffffff;
}

.parts-table-arrows-up {
  background: url("../img/icons/sort-arrow.svg") center center/contain no-repeat;
  display: block;
  width: 0.625rem;
  height: 0.5rem;
  margin-top: 0.875rem;
}

.parts-table-arrows-up-active {
  background: url("../img/icons/sort-arrow-active.svg") center center/contain no-repeat;
}

.parts-table-arrows-up-inactive {
  display: none;
}

.parts-table-arrows-down {
  background: url("../img/icons/sort-arrow.svg") center center/contain no-repeat;
  transform: rotate(180deg);
  display: block;
  width: 0.625rem;
  height: 0.5rem;
}

.parts-table-arrows-down-active {
  margin-top: 1.375rem;
  background: url("../img/icons/sort-arrow-active.svg") center center/contain no-repeat;
}

.parts-table-arrows-down-inactive {
  display: none;
}

.editor-parts {
  padding: 1.875rem;
  width: 33.75rem;
  min-height: 15rem;
  background: #C4C4C4;
  position: absolute;
  top: 3.125rem;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 1.25rem;
  z-index: 5;
}

.editor-parts-btns {
  margin-top: 1.875rem;
  display: flex;
  justify-content: space-between;
}

.btn {
  padding: 0;
  font-weight: 400;
  color: #ffffff;
  font-size: 0.875rem;
  line-height: 1rem;
  border: none;
  border-radius: 3.125rem;
  width: 6.875rem;
  height: 1.875rem;
  cursor: pointer;
  background: #706565;
  outline: none;
}

.btn_account {
  margin-top: 0.9375rem;
  margin-left: 0.625rem;
}

.btn_add {
  width: 200px;
  height: 30px;
}

.btn:hover {
  background: #413E3E;
}

.btn:active {
  background: #000000;
}

.btn_modal {
  width: 16.25rem;
  height: 2.5rem;
  background: #0049B7;
  border-radius: 3.125rem;
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 2.5rem;
  text-align: center;
  color: #ffffff;
}

.btn_modal:hover {
  background: #4E65B9;
}

.btn_modal:active {
  background: #0C226D;
}

.btn_modal-cancel {
  width: 16.25rem;
  height: 2.5rem;
  background: #706565;
  border-radius: 3.125rem;
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 2.5rem;
  text-align: center;
  color: #ffffff;
}

.btn_modal-cancel:hover {
  background: #413E3E;
}

.btn_modal-cancel:active {
  background: #000000;
}

.overflow {
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal-title {
  width: 33.75rem;
  height: 2.5rem;
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 2.5rem;
  text-align: center;
  color: #000000;
  position: relative;
}

.modal-title:after {
  content: "";
  width: 33.75rem;
  height: 0.125rem;
  background: #000000;
  position: absolute;
  top: 2.5rem;
  left: 50%;
  transform: translateX(-50%);
}

.modal-field {
  width: 33.75rem;
  margin-top: 1.25rem;
}

.modal-field-title {
  display: block;
  height: 2.1875rem;
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 2.1875rem;
  color: #000000;
}

.modal-field-value {
  display: block;
  width: 31.25rem;
  min-height: 2.1875rem;
  border: 1px solid #C4C4C4;
  border-radius: 1.25rem;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.1875rem;
  color: #000000;
  padding: 1.25rem;
  outline: none;
  resize: none;
}

.main,
.departments,
.cartridges,
.toners,
.drums,
.magnetic-shafts,
.primary-charge-shafts,
.cleaning-blades,
.dispensing-blades,
.tools {
  padding: 1.875rem;
  flex-grow: 1;
}