* {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, ul {
  margin: 0;
  padding: 0;
}

.container-content {
  display: flex;
  justify-content: flex-start;
}

.even {
  background: $even;
}

.odd {
  background: $odd;
}

.hide {
  display: none;
}
